@keyframes growDown {
  0% {
    transform: scaleY(0.5);
  }
  // 80% {
  //     transform: scaleY(1)
  // }
  100% {
    transform: scaleY(1);
  }
}

@keyframes animatedBackground {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@keyframes up-fade {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(1);
  }
}

@keyframes drop-fade {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(1);
  }
}

/*----------------*/

@keyframes shrinkUp {
  0% {
    transform: scaleY(1);
    visibility: visible;
    height: 100px;
    // height: 100%;
  }

  100% {
    transform: translateY(-100%);
    position: absolute;
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    height: 0;
  }
}
@keyframes growFill {
  0% {
    opacity: 0.3;
    height: 0;
  }
  100% {
    opacity: 1;
    height: 700px;
  }
}

@keyframes shrinkFill {
  0% {
    opacity: 1;
    height: 580px;
  }

  100% {
    opacity: 0.3;
    height: 0;
    padding: 0;
  }
}

