// Evil bot
.evil-bot__container {
  position: absolute;
  // border: solid 1px red;
  top: 780px;
  right: 0;
  display: flex;
  align-items: center;
  z-index: 10;

  @media all and (max-width: 768px) {
    // Appear when scroll...
    position: absolute;
    top: 520px;
    margin: auto;
  }

  @media all and(max-width: 55px) {
    position: absolute;
    bottom: 353px;
    margin: auto;
  }
}

.evil-chat__container {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(115, 60, 187, 0.3);
  background-color: rgba(204, 196, 214, 0.959);
  min-height: 60px;
  max-width: 300px;
  border-radius: 6px;
  position: relative;
  padding: 5px 20px;
  color: rgb(86, 11, 136);
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.32);

  text-align: center;

  @media all and (max-width: 410px) {
    width: 250px;
    font-size: 14px;
  }

  &::after {
    content: '';
    height: 10px;
    width: 10px;
    position: absolute;
    right: -5px;
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
    background-image: linear-gradient(
      -130deg,
      rgba(115, 60, 187, 0.3) 50%,
      rgba(0, 0, 0, 0) 50%
    );
  }
}

.evil-bot {
  width: 100px;
  height: 100px;
  background-image: url('https://i.imgur.com/9bukl8R.png');
  background-size: 50px;
  background-position: center;
  background-repeat: no-repeat;
  &:hover {
    cursor: help;
  }

  // border: solid 1px red;
}

.fadeout {
  animation: fadeout 2s ease forwards;
}

.fadein {
  animation: fadein 2s ease forwards;
}

@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.talk {
  background-image: url('https://i.imgur.com/w99oqTS.png') !important;
  background-size: contain;
}

.stop {
  background-image: url('https://i.imgur.com/9bukl8R.png');
}
